.backdrop {
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px;
    z-index: 150;
    transition: var(--timing-function);
    opacity: 1;

    @media screen and (max-width: 668px) {
        display: block;
    }
}

.backdrop--hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
}

.modal-overlay {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 99;
    transition: var(--timing-function);

    &.show {
        opacity: 1;
        visibility: visible;
    }
}

.modal {
    position: relative;
    width: 100%;
    max-width: 414px;
    background: var(--light-background);
    padding: 60px 15px;
    margin: 0 auto;
    z-index: 30;
    overflow-y: auto;
    border: 1px solid var(--color-primary);
    border-radius: 2px;

    @media screen and (min-width: 1024px) {
        max-width: 712px;
        padding: 60px;
    }

    /* opacity: 0;
  visibility: hidden; */

}

.modal__button {
    position: absolute;
    top: 16px;
    right: 16px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    cursor: pointer;

    stroke: var(--color-black);
    border: none;
    background-color: transparent;
    transition: var(--timing-function);
}

.modal__button:hover,
.modal__button:focus {
    stroke: #808080;
}

.modal__button:hover .modal__icon {
    stroke: #808080;
}

.modal__button:focus .modal__icon {
    stroke: #808080;
}

.modal__icon {
    fill: var(--color-black);
}

.modal__title {
    margin-bottom: 40px;

    font-family: var(--font-subhead);
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.17;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: var(--color-black);

    @media screen and (min-width: 1024px) {
        font-size: 40px;
        line-height: 1.15;
    }
}

.form {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.form__item {
    display: flex;
    flex-wrap: wrap;
    
    @media screen and (min-width: 1024px) {
        gap: 16px;
}
}

.form__label {
    width: 100%;
    margin-bottom: 20px;

        @media screen and (min-width: 1024px) {
    width: calc((100% - 16px) / 2);
    margin-bottom: 28px;

     &:nth-of-type(2) {
        order: 1;
                }
        
    &:nth-of-type(4) {
        order: 3;
                }
            }
}

.form__label-number {
    width: 100%;
    margin-bottom: 20px;

@media screen and (min-width: 1024px) {
        width: calc((100% - 16px) / 2);
        margin-bottom: 28px;

        &:nth-of-type(3){
            margin-left: 305px;
        }
}
}

.form__input {
    display: block;
    width: 100%;
    padding: 17px 12px;
    max-height: 54px;
    margin-bottom: 8px;

    font-family: var(--font-subhead);
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 1.15;
    letter-spacing: 0.05em;
    color: var(--color-black);
    border: 1px solid var(--color-primary-dark);
    border-radius: 2px;

    @media screen and (min-width: 1024px) {
        max-height: 64px;
        font-size: 24px;
        line-height: 1.16;
    }
}

.form__text {
    margin-left: 12px;

    @media screen and (min-width: 1024px) {
        margin-left: 14px;
    }
}

.form__input::placeholder {
    font-family: var(--font-subhead);
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 1.15;
    letter-spacing: 0.05em;
    color: #808080;

    @media screen and (min-width: 1024px) {
        font-size: 24px;
        line-height: 1.16;
    }
}

