.form__select {
    position: relative;
}
.select {
    appearance: none;
    display: block;
    padding: 17px 12px;
    width: 100%;
    max-height: 54px;
    margin-bottom: 8px;
    font-family: var(--font-subhead);
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 1.15;
    letter-spacing: 0.05em;
    color: #808080;
    border: 1px solid var(--color-primary-dark);
    border-radius: 2px;

    @media screen and (min-width: 1024px) {
        max-height: 64px;
        font-size: 24px;
        line-height: 1.16;
    }
}

.select__value {
        font-family: var(--font-subhead);
            font-style: normal;
            font-weight: 700;
            font-size: 20px;
            line-height: 1.15;
            letter-spacing: 0.05em;
            color: #808080;
            align-items: baseline;

            @media screen and (min-width: 1024px) {
                    max-height: 64px;
                    font-size: 24px;
                    line-height: 1.16;
                }
}

.form__select::after {
    content: '';
    position: absolute;
    // display: inline-block;
    top: 15px;
    right: 15px;
    width: 24px;
    height: 24px;
    z-index: 100;
    background-image: url("../images/ep_arrow-down-bold.svg");
    pointer-events: none;
    background-size: contain;
}

.form__button {
    display: block;
    height: 64px;
    padding: 20px 0;

    font-family: var(--font-main);
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.2;
    text-transform: uppercase;
    cursor: pointer;
    color: var(--color-white);
    background: var(--color-black);
    border: 1px solid var(--color-primary);
    border-radius: 2px;
    margin-top: 20px;
    transition: 300ms;

    @media screen and (min-width: 1024px) {
        padding: 18px 0;
        font-size: 24px;
        line-height: 1.21;
        margin-top: 12px;
    }
}

.form__button:hover,
.form__button:focus {
    color: var(--color-black);
    background: var(--color-primary);
}

