*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --font-main: "Montserrat", sans-serif;
  --font-logo: "Film P3", sans-serif;
  --font-subhead: "Times New Roman", sans-serif;
  --color-primary: #f3c653;
  --color-primary-dark: #c79002;
  --color-brown: #c79002;
  --color-dark: #494848;
  --color-beige1: #5f5f5f;
  --color-beige2: #7a7a7a;
  --color-beige3: #828282;
  --color-beige4: #b4b4b4;
  --color-beige5: #bababa;
  --color-white: #fff;
  --color-black: #000;
  --color-mobile-logo: #fdfbec;
  --color-border-social: rgba(243, 198, 83, .7);
  --sense-background: #f0ecd6;
  --light-background: #fffdee;
  --dark-background: #0e0c07;
  --button-border: 2px solid #fff;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --mobile: 414px;
  --mobile-less: 413px;
  --tablet: 1024px;
  --tablet-less: 1023px;
  --desktop: 1280px;
  --desktop-less: 1279px;
  --vertical-lines-desktop: repeating-linear-gradient( 90deg, rgba(186, 186, 186, .4) 1px, transparent 2px, transparent 302px );
  --vertical-lines-tablet: repeating-linear-gradient( 90deg, rgba(186, 186, 186, .4) 1px, transparent 2px, transparent 234px );
  --vertical-lines-mobile: repeating-linear-gradient( 90deg, rgba(186, 186, 186, .4) 1px, transparent 2px, transparent 134px );
}

